import {Registry} from 'cs-web-components-base';
import DocumentCreateWidget from './DocumentCreateWidget';
import {
    ClassViewWithFiles, OperationModalWithFiles, FormWithFiles
} from './FileOperationHandlers';
import {prefixNS} from './helpers.js';
import RecentlyModifiedDocumentsWidget from './dashboard/RecentlyModifiedDocumentsWidget.jsx';
import DocTemplateCreateApp from './containers/DocTemplateCreate.jsx';

Registry.registerComponent(prefixNS('ClassViewWithFiles'), ClassViewWithFiles);
Registry.registerComponent(prefixNS('OperationModalWithFiles'), OperationModalWithFiles);
Registry.registerComponent(prefixNS('FormWithFiles'), FormWithFiles);
Registry.registerComponent(prefixNS('DocumentCreateWidget'), DocumentCreateWidget);
Registry.registerComponent(prefixNS('RecentlyModifiedDocumentsWidget'),
    RecentlyModifiedDocumentsWidget);
Registry.registerComponent(prefixNS('DocTemplateCreateApp'), DocTemplateCreateApp);

export {
    DocumentCreateWidget,
    ClassViewWithFiles,
    DocTemplateCreateApp,
    RecentlyModifiedDocumentsWidget
};



